




































import { Component, Vue } from "vue-property-decorator";
import { getUserList } from "../../../api/system";
@Component({
  components: {}
})
export default class InfoUser extends Vue {
  list: any = [];
  total: any = null;
  currentPage = 1;
  handleSizeChange(a: any) {
    console.log(a);
  }
  handleCurrentChange(a: any) {
    console.log(a);
  }
  async search() {
    const { total, list } = await getUserList();
    this.total = total;
    this.list = list;
  }
  async created() {
    this.search();
  }
}
